.header {
	margin-top: .5rem;
	padding-left: 10px;
}

.header ul li {
	list-style: none;
	margin: 0;
	padding: .5rem 1rem;
}

.header ul li:hover {
	background-color: #444;
	border-radius: 3px;
}

.header a:hover {
	text-decoration: none;
}

.header h3 a {
	color: white;
	white-space: nowrap;
}

.header h3 a:hover {
	color: #00bc8c;
}

.header button {
	height: 2.2rem;
	margin-top: 3px;
	white-space: nowrap;
}